/* center */

.sloothAlertcenter1 {
  border: 1px solid lightgrey;
  padding: 0;
  margin: 0px 0px 0px 111px !important;
  width: 121% !important;
}
.heading-logo {
  display: flex;
  justify-content: center;
  margin-top: 5%;
}
.services-logo {
  width: 4%;
}
.services {
  margin: 5% 0 5% 1%;
  color: #2a2c7c;
}
.search-bar2 {
  height: 3%;
  width: 90%;
  margin: 0% 0 0 5%;
  border: 1px solid #2a2c7c;
}
.fa2 {
  display: flex;
  color: #2a2c7c;
}
.form-control2s {
  border: 0 !important;
  border-radius: 24px !important;
  height: 2% !important;
}
.showfilter {
  color: #2a2c7c;
}
.image-showfilter {
  display: inline-flex;
  margin: 4% 8% 0% 5%;
}
.sortby-image {
  display: inline-flex;
  margin: 4% 0 0% 49%;
  cursor: pointer;
}
.all-images-paragraphs {
  display: flex;
  flex-direction: row;
  cursor: pointer;
}

.market-navtab {
  display: flex;
  justify-content: space-around;
}
.furniture {
  text-decoration: none;
  color: gray;
  font-weight: bold;
}
.furniture:hover {
  cursor: pointer;
  text-decoration: none;
  color: gray;
}
.houses {
  color: #2a2c7c;
  text-decoration: none;
  font-weight: bold;
}
.houses:hover {
  cursor: pointer;
  color: #2a2c7c;
  text-decoration: none;
  font-weight: bold;
}

.container {
  position: relative;
  text-align: center;
  color: white;
  display: flex;
  justify-content: space-between;
}

.doghouses {
  border: 10px solid white;
  border-radius: 20px;
  margin-left: 2%;
  width: 100%;
  height: 210px;
}

.all-left-doghouses {
  display: flex;
  flex-direction: column;
}

.all-right-doghouses {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.left-doghouses {
  position: absolute;
  bottom: 399px;
  left: 45px;
  display: none;
}
.right-doghouses {
  position: absolute;
  bottom: 399px;
  right: 170px;
  display: none;
}
.bottom-left-doghouses {
  position: absolute;
  bottom: 205px;
  left: 45px;
}

.bottom-right-doghouses {
  position: absolute;
  bottom: 205px;
  right: 160px;
}

.top-left-doghouses {
  position: absolute;
  left: 45px;
  bottom: 20px;
}

.top-right-doghouses {
  position: absolute;
  right: 170px;
  bottom: 20px;
}

.petname-doghouses {
  color: white;
  position: absolute;
  margin-top: -9%;
  margin-left: 6%;
}

.marketplace-item{
  cursor: pointer;
}
