

@font-face {
  font-family: 'Montserrat-Bold';
  src: url("./assets/fonts/Montserrat-Bold.ttf");
}

.main-search {
  width: 95% !important;
}

.search-bar {
  height: 3%;
  width: 89%;
  margin: 3% 5% 5% 5%;
}

.bbrr {
  margin: 12% 0 0 6%;
}

.top {
  height: 100px;
  width: 30%;
  cursor: pointer;
}

.down {
  height: 100px;
  width: 30%;
  cursor: pointer;
}
.left {
  border-top-left-radius: 15px;
}
.right {
  border-top-right-radius: 15px;
}
.d-left {
  border-bottom-left-radius: 15px;
}
.d-right {
  border-bottom-right-radius: 15px;
}

.bottom-border {
  border: 1px solid lightgray;
  border-radius: 15px;
  margin: 8% 0 0 6%;
  width: 86%;
  /* background-color: rgb(230, 236, 240); */
  background-color: #f7fafb;
}
.pet10 {
  width: 60px;
  height: 57px;
  margin: 9% auto 0;
  border-radius: 80%;
  position: relative;
  margin: 4% 3% 13%;
}
.heading-font {
  font-weight: bold;
  margin: 5%;
}

.images-right {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin: -7% 5% -15% 1%;
}

.right-border {
  margin: 6% 0 0 2%;
}
.name-br {
  color: black;
}
.name-br:hover {
  color: black;
}
.puppy-mail {
  color: gray;
  font-size: 0.8rem;
}
.btn-right {
  margin: 31% 0 0 2%;
  background-color: rgb(230, 236, 240) !important;
  border: 1px solid rgba(29, 161, 242, 1) !important;
  color: rgba(29, 161, 242, 1) !important;
  border-radius: 16px !important;
}
.btn-right:hover {
  background-color: rgba(29, 161, 242, 1) !important;
  border: 1px solid white !important;
  color: white !important;
  border-radius: 16px !important;
}

.down-anc {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin: 5%;
}

.buttons-follow {
  display: flex;
  justify-content: flex-end;
}

.para-down {
  color: gray;
  cursor: pointer;
}

.para-bold {
  font-weight: bold;
  cursor: pointer;
}

.paaa {
  margin: -6% 8% 2% 0%;
  cursor: pointer;
}

.follo-butn {
  border-radius: 16px !important;
  height: 6vh;
}

.envilope-butn {
  border-radius: 16px !important;
  height: 6vh;
}

.arrow-down {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin: -22% 15% 17% 0;
  font-size: 1.3rem;
  cursor: pointer;
}
.full-right {
  margin: 5%;
}

.pet-foot {
  height: 4vh;
}

.user {
  font-size: 1.3rem;
  font-weight: bold;
  /* font-family: 'Montserrat-Bold'; */
}

.anchor:hover {
  text-decoration: none;
}
.anchor{
  font-family: 'Montserrat-Bold';
}
.groups {
  margin-left: 6%;
}

.market {
  margin-left: -6%;
}

.set {
  margin-left: -5%;
}
.foam {
  border-radius: 138px !important;
  height: 34px !important;
}
.search-rightside{
  height: 4%;
}
.search-right{
  font-size: 0.7rem !important;
}
.top, .down{
  margin: 0.3%;
}


@media(max-width: 817px) {
  .main-search{
    display: none;
  }
}

@media(max-width: 877px) {
  .main-search{
    display: none;
  }
}